import React, { Component } from 'react';

import { FormGroup,FormControl,ControlLabel } from 'react-bootstrap';

class FormInstance extends Component {
  constructor() {
    super();
    this.state = {
        username: null,
        password: null,
    };

    this.handleUsernameChange = this.handleUsernameChange.bind(this);
    this.handlePasswordChange = this.handlePasswordChange.bind(this);

    this.handleKeyPress = this.handleKeyPress.bind(this);
  }

  handleKeyPress(event) {
    if(event.key === 'Enter' && this.state.username && this.state.password){
        document.getElementById("log-me-in").click();
        this.setState({password:''});
    }
  }

  handleUsernameChange(event) {
     this.setState({username: event.target.value});
  }

  handlePasswordChange(event) {
     this.setState({password: event.target.value});
  }

  componentDidMount() {
//    document.getElementById("formHorizontalEmail").focus();
  }

  dummy(e) {
    e.preventDefault();
  }

  render() {
    var login = this.dummy;
    if (this.state.username && this.state.password) {
        login = this.props.login.bind(this.props.retThis,this.state.username,this.state.password);
    }
    return (
   <form>
    <div id="login-form" style={{"background":"#fff","opacity":"0.7","marginTop":"0px","padding":"0 0","fontFamily":"Roboto,Lucida Grande,DejaVu Sans,Bitstream Vera Sans,Verdana,Arial,sans-serif"}}>
    <div style={{ "padding":"0 20px","marginTop":"0px"}}>
		<FormGroup controlId="formHorizontalEmail">
			<ControlLabel>Username</ControlLabel>
			<FormControl type="text" autoComplete="off" onChange={this.handleUsernameChange} onKeyPress={this.handleKeyPress} spellCheck="false" />
		</FormGroup>
		<FormGroup controlId="formHorizontalPassword">
			<ControlLabel>Password</ControlLabel>
			<FormControl type="password" onChange={this.handlePasswordChange} onKeyPress={this.handleKeyPress} />
		</FormGroup>
        <section className="page-header" style={{"padding":"0"}}>
            <a style={{"width":"100%","height":"100%","margin":"0"}} onClick={login} className="btn" id="log-me-in" href={this.props.trusted ? "/login-me-in/" : ""}>Sign in</a>
        </section>
        <span style={{"float":"right","fontSize":"11px"}}><a href="/probable-chainsaw/">Forgotten password?</a></span>
	</div>
  </div>
  </form>
    );
  }
}

export default FormInstance;



