import React, { Component } from 'react';

class MainNav extends Component {
  constructor(props) {
    super(props);

    this.state = {
        mainNav: '',
    };
  }

  componentDidMount() {
    fetch('https://raw.githubusercontent.com/anothergithubber/anothergithubber.github.io/master/_includes/main-nav.html', {
      headers: {
      'Accept': 'text/html',
      },
    })
    .then(function(res) {
        return res.text();
    }).then(function(res) {
        if (res.length > '') {
            this.setState({ mainNav: res });
        }
    }.bind(this));
  }

  render() {
    return (
    <div className="MainNav">
        <div className="test"></div>
        <div dangerouslySetInnerHTML={{__html: this.state.mainNav}} />
    </div>
    );
  }
}

export default MainNav;



