import React, { Component } from 'react';
import MainNav from './MainNav';

import FormInstance from './FormInstance';
import AppLayout from './AppLayout';

class App extends Component {

  constructor() {
    super();
    this.state = {sessionId:'',content:null,auth:false,
                ip:'guest',trusted:0};
    this.logout = this.logout.bind(this);
//    this.unlock = this.unlock.bind(this);
  }

  isAuth() {
    fetch('https://bw3.bitweevil.com/is-auth.php', {
      credentials: 'include',
      mode: 'cors',
      headers: {
      'Accept': 'text/html',
      },
    })
    .then(function(res) {
        return res.json();
    }).then(function(res) {
        if (res.valid) {
            this.setState({sessionId:res.sessionId,auth:true});
            this.fetchAuthenticatedContent();
        } else {
            var content = <div style={{"padding":"0px"}}><FormInstance login={this.login} retThis={this}/></div>;
            this.setState({content:<AppLayout id="login-component" title={"Login"} content={content} />,auth:false});
        }
        document.getElementById("App").style.visibility = "visible";
//        document.getElementById("twitter-follow-button").style.visibility = "visible";
//        document.getElementById("twitter-follow-button").style.display = "block";
        if (!res.valid) {
            document.getElementById("formHorizontalEmail").focus();
        }

        const script = document.createElement("script");

        script.src = "//platform.twitter.com/widgets.js";
        script.async = true;

        document.body.appendChild(script);

    }.bind(this));
  }


  fetchAuthenticatedContent() {
    fetch('https://bw3.bitweevil.com/get-my-stuff.php', {
      credentials: 'include',
      mode: 'cors',
      method: 'post',
      body: JSON.stringify({
          'sessionId': this.state.sessionId,
//          'ip': '',
      }),
      headers: {
      'Content-Type':'application/x-www-form-urlencoded',
      'Accept': 'text/html',
      },
    })
    .then(function(res) {
        return res.json();
    }).then(function(res) {
        if (res.valid) {
            this.setState({content:res.content});
        }
    }.bind(this));
  }

  getRemoteAddress() {
    fetch('https://bw3.bitweevil.com/get-remote-address.php', {
      credentials: 'include',
      headers: {
          'Content-Type':'application/x-www-form-urlencoded',
          'Accept': 'text/html',
      },
    })
    .then(function(res) {
        return res.json();
    }).then(function(res) {
        var trusted = 0;
        if (res.status === 'trusted') {
            trusted = 1;
        }
        this.setState({ ip: res.ip, trusted: trusted });
        this.isAuth();
    }.bind(this));
  }

  componentDidMount() {
    this.getRemoteAddress();
    var date = "Sat 29 Jul 13:07:38 BST 2017";
    console.log('date: '+date);
  }

  login(username, password, e) {
    e.preventDefault();
    fetch('https://bw3.bitweevil.com/log-me-in.php', {
      credentials: 'include',
      mode: 'cors',
      method: 'post',
      body: JSON.stringify({
          'username': username,
          'password': password,
          'ip': this.state.ip,
      }),
      headers: {
      'Content-Type':'application/x-www-form-urlencoded',
      'Accept': 'text/html',
      },
    })
    .then(function(res) {
        return res.json();
    }).then(function(res) {
        if (res.valid) {
            this.setState({sessionId:res.sessionId,auth:true});
            this.fetchAuthenticatedContent();
        } else {
            document.getElementById("formHorizontalPassword").value = '';
        }
    }.bind(this));
  }

  logout(e) {
    e.preventDefault();
    fetch('https://bw3.bitweevil.com/log-me-out.php', {
      credentials: 'include',
      mode: 'cors',
      method: 'get',
      headers: {
      'Content-Type':'application/x-www-form-urlencoded',
      'Accept': 'text/html',
      },
    })
    .then(function(res) {
        return res.json();
    }).then(function(res) {
        if (res.valid) {
            var content = <div style={{"padding":"0px"}}><FormInstance login={this.login} retThis={this} /></div>;
            this.setState({sessionId:'',content:<AppLayout id="login-component" title={"Login"} content={content} trusted={this.state.trusted} />,auth:false});
        }
    }.bind(this));
  }

  unlock(e) {
    e.preventDefault();
    fetch('https://bw3.bitweevil.com/unlock-test-account.php', {
      credentials: 'include',
      mode: 'cors',
      method: 'get',
      headers: {
      'Content-Type':'application/x-www-form-urlencoded',
      'Accept': 'text/html',
      },
    })
    .then(function(res) {
        //return res.json();
        return null;
    }).then(function(res) {
    });
  }

  description() {
    return (
        <div>
        <section className="main-content">
       <div style={{"clear":"both","overflow":"hidden","width":"100%"}}>
           <div style={{"float":"left","display":"inline-block","width":"80%"}}>Sign in with the test account (username: test, password: broccoli). After 3 failed attempts the account will be locked. Click Unlock to reactivate the account.</div>
           <div style={{"float":"left","display":"inline-block","width":"20%"}}><span style={{"float":"right"}}><button disabled={this.state.trusted ? 0 : 1} onClick={this.unlock}>Unlock</button></span></div>
       </div>
       <div className="clearfix"></div>
        </section>
        {this.state.content}
        </div>
    );
  }

  render() {
    var content = null;
    if (this.state.content && this.state.auth) {
        var logout = <span style={{"float":"right"}}><button onClick={this.logout}>Log out</button></span>;
        var contentSpan = <span dangerouslySetInnerHTML={{__html: this.state.content}}></span>;
        content = <section className="main-content">{contentSpan}{logout}</section>;
    } else if (this.state.content && !this.state.auth) {
//        content = <section className="main-content">{this.state.content}</section>;
        content = this.description();
        //content.push(this.state.content);
    }
    return (
      <div id="App" style={{"visibility":"hidden"}}>
       <MainNav />
        {content}
        <section className="main-content">

            <span>
                <a href="https://twitter.com/anothergithubbe" data-show-screen-name="false" className="twitter-follow-button" id="twitter-follow-button" style={{"verticalAlign":"middle !important","display":"none"}} data-show-count="false">Follow</a>
            </span>

                <span className="site-footer-social">
                    <div className="fb-like" style={{"verticalAlign":"top","zoom":"1","*display":"inline"}} data-href="https://anothergithubber.github.io/ubiquitous-broccoli/" data-layout="button" data-action="like" data-size="small" data-show-faces="false" data-share="true"></div>
                </span>

          <footer className="site-footer">
            <span className="site-footer-credits"><a href="https://github.com/anothergithubber/anothergithubber.github.io">ubiquitous-broccoli</a> by <a href="https://github.com/anothergithubber">anothergithubber</a>.</span>
          </footer>
        </section>
      </div>
    );
  }
}

export default App;



